@function q($px) {
    @return ($px / 46.875)+rem;
}

.big_all_list { width: 100%; box-shadow: 0 2px 2px #e5e5e5; height: auto; background: #fff; margin-bottom: q(25);
    .out_list {  width: 100%; line-height: q(90); border-bottom: 1px solid #e3e3e3;
        .left_list { display: inline-block; width: 48%; text-align: left; padding-left: q(40); box-sizing: border-box;
            img { width: q(31); height: q(27);display: inline-block; margin-right: q(15);}
            span { font-size: q(32); color: #3aa5ff;}
        }
        .right_list { display: inline-block; width: 48%; text-align: right; padding-right: q(40); box-sizing: border-box;
            img { width: q(31); height: q(27);display: inline-block; margin-right: q(15);}
            a { font-size: q(28); color: #ef4443;}
        }

    }
    .out_list_small { height: q(85); vertical-align: middle; border-bottom: 1px solid #e3e3e3; margin: 0 q(40); box-sizing: border-box;
        .left_final { width: 37%; display: inline-block; text-align: left; font-size: q(28); color: #3d3d3d; box-sizing: border-box; vertical-align: middle;}
        .right_final { background: #fff; display: inline-block; width: 60%; text-align: right; font-size: q(28); color: #666; height: q(82); outline: none; box-sizing: border-box;}
        input[disabled],input:disabled,input.disabled {
            color: #666;
            -webkit-text-fill-color: #666;
            -webkit-opacity: 1;
            opacity: 1;
        }
        .span_suz { width: 63%; display: inline-block; vertical-align: -webkit-baseline-middle; font-size: q(28); color: #666; box-sizing: border-box; height: auto;  word-break: break-all;  text-align: right;}
        input::-ms-input-placeholder { font-size: q(28); color: #ccc;}
        input::-webkit-input-placeholder { font-size: q(28); color: #ccc;}
        input::-moz-placeholder { font-size: q(28); color: #ccc;}
        &.border_none { border-bottom: none;}

    }
    .out_list_smal { height: q(175); width: 100%; padding: q(30) q(40); box-sizing: border-box; display: flex;

        .brief_introduction { width: 100%; font-size: q(28); color: #999;}

    }
    ul.out_list_sma { height: auto; width: 100%; padding: q(30) q(40); box-sizing: border-box; display: flex; flex-wrap: wrap; justify-content: flex-start;
        .circle_radius { flex: 0 1 q(170); margin-right: q(35); margin-bottom: q(20); text-align: center; border-radius: q(25); background: #ef4443;
            a { color: #fff; font-size: q(30); height: q(57); width: q(170); line-height: q(57); display: inline-block;}
        }
    }
    .out_list_photo { width: 100%; height: auto; padding: q(30) q(40); box-sizing: border-box;
        a { display: inline-block;
            img { width: q(124); height: q(124); display: block; }
            .photo_under_font { font-size: q(28); color: #999; display: inline-block; margin-top: q(20); width: 100%; text-align: center;}
        }

     }
}
.winbox_photo { z-index: 3; display: none; position: fixed; right: 0; top: 0; left: 0; bottom: 0; background: #000; opacity: .6;}
.wechat_big_box { z-index: 4; display: none;  width: 100%; text-align: center; position: fixed; top: q(90); box-sizing: border-box; }
.big_box_photo { position: relative; width: q(680); height: q(890); background: #fff; border-radius: q(20); margin: 0 auto; padding: q(40); box-sizing: border-box;
    p { font-size: q(24); color: #666; margin-top: q(40);}
    .top_box { width: 100%; height: auto; margin-bottom: q(20);
        .left_box { text-align: left;
            img { width: q(158); height: q(158); display: inline-block; border-radius: 50%;}
            .soan_box { display: inline-block; vertical-align: top; height: q(158); width: 65%; padding-top: q(30); text-align: left; margin-left: q(20);
                h1 { font-size: q(40); color: #333; margin-bottom: q(10);}
                span { font-size: q(28); color: #666; }
            }
        }
        .right_box { padding: q(20); width: q(20); height: q(20); display: block; position: absolute; right: q(20); top: q(10);}
    }
    .photo_bottom { display: block; width: q(540); height: q(530); margin: 0 auto;}
}

.big_winbox {
    .winbox_photo { z-index: 3; display: block; position: fixed; right: 0; top: 0; left: 0; bottom: 0; background: #000; opacity: .6;}
    .wechat_big_box { display: block; z-index: 4; width: 100%; position: absolute; top: q(90); box-sizing: border-box; }
    .big_box_photo { position: relative; width: q(680); height: auto; background: #fff; border-radius: q(20); margin: 0 auto; padding: q(40); box-sizing: border-box;}
}
.out_list_bottom { margin-bottom: q(120);}
.clas_font_color { color: #3d3d3d!important;}

