@function q($px) {
    @return ($px / 46.875)+rem;
}
.order_list { position: relative;
    .order_box { display: block; margin: q(40); background: #fff; padding: q(40) q(20) q(30); border-radius: q(20); position: relative;
        .pay_top { padding-bottom: q(40); border-bottom: 1px dashed #ccc; margin-bottom: q(30);
            .pay_title { margin-bottom: q(50);
                i { display: inline-block; width: q(44); height: q(44); border-radius: 100%; background: url('../images/list_photo.png') no-repeat 0 0; background-size: 100% 100%; vertical-align: middle;}
                p { display: inline-block; font-size: q(40); vertical-align: middle;}
            }
            ul {
                li { display: flex; padding: q(15) q(0);
                    span { font-size: q(24); color: #333;
                        &.bold { margin-left: q(10); flex: 1;}
                    }
                    .pname { background: url('../images/list_icon1.png') no-repeat 0 0; background-size: q(22) q(24); background-position: q(0) q(0); padding-left: q(30); margin-right: q(10);}
                    .ptime { background: url('../images/list_icon2.png') no-repeat 0 0; background-size: q(23) q(23); background-position: q(0) q(0); padding-left: q(31); margin-right: q(10);}
                    .bnum { background: url('../images/list_icon3.png') no-repeat 0 0; background-size: q(24) q(22); background-position: q(0) q(0); padding-left: q(32); margin-right: q(10);}
                    .tbnum { background: url('../images/list_icon4.png') no-repeat 0 0; background-size: q(24) q(22); background-position: q(0) q(0); padding-left: q(32); margin-right: q(10);}
                }
            }
            img.bg { position: absolute; width: q(135); height: q(119); top: q(10); right: q(-5);}
        }
        .pay_bottom { display: flex; justify-content: space-between; align-items: center;
            p {
                span { font-size: q(22); color: #666;
                    &.labe { font-size: q(40); color: #f33939; margin-left: q(10); }
                    &.num { font-size: q(50); color: #f33939;}
                }
            }
        }
    }
}

 .pay_btn._pay_btn { position: absolute; bottom: q(30); right: q(60);
    span { display: inline-block; font-size: q(24); width: q(123); height: q(50); line-height: q(50); text-align: center; border: 1px solid #ccc; border-radius: q(15);}
    .pay_imm { background: #ef4443; color: #fff; border: none; margin-left: q(14);}
}
.box_wrapper { margin-bottom: q(50);}
