.service_attache {
    .sa_title { display: flex; align-items: center; justify-content: space-between; padding: q(30) q(40); background-color: #fff;
        .sa_left { font-size: 0;
            .icon { display: inline-block; vertical-align: middle; width: q(26); height: q(26);
                img { display: block; width: 100%;}
            }
            h4 { display: inline-block; vertical-align: middle; font-size: q(32); color: #f16c2b; margin-left: q(15);}
        }
        .sa_right { font-size: 0; vertical-align: middle;
            .name { display: inline-block; vertical-align: middle; font-size: q(28); color: #333;}
            .icon { display: inline-block; vertical-align: middle; width: q(22); height: q(12); margin-left: q(18);

                &.rotate {
                    img { display: block; width: 100%; transform: rotateZ(180deg);}
                }

                img { display: block; width: 100%; transition: .6s ease all;}
            }
        }
    }
    .sa_content { margin: q(20) q(40); border-radius: q(10); border-top: q(4) solid #1e94ef; box-shadow: 0 q(10) q(20) #efefef; background: url('../images/sa_bg_img.png') left bottom no-repeat; background-size: q(340) q(340); background-color: #fff; display: none;
        .content_title { display: flex; align-items: center; justify-content: space-between; margin: 0 q(30) 0 q(40); padding: q(38) 0 q(45); border-bottom: 1px dashed #dadada;
            .sacon_title_left { margin-left: q(46);
                .photo { width: q(220); height: q(220); border-radius: 50%;}
                .name { text-align: center; font-size: q(28); color: #808080; line-height: q(40); margin-top: q(36);}
            }
            .sacon_title_right { text-align: right;
                .logo { font-size: 0;
                    img { display: inline-block; width: q(245); height: q(58);}
                }
                .qr_code { margin-top: q(34); margin-bottom: q(14); font-size: 0;
                    img { display: inline-block; width: q(170); height: q(170);}
                }
                .prompt { font-size: q(24); color: #999;}
            }
        }
        .content_container { margin-top: q(32); margin-bottom: q(30);
            li { display: flex; align-items: center; justify-content: space-between;  margin: 0 q(30) 0 q(40);
                .sacc_left { width: q(105); font-size: q(26); color: #363636; line-height: q(55);}
                .sacc_right { font-size: q(26); color: #808080;}
            }
        }
    }
}
